.bg-full-block {

	img {
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}
.main-title {
	font-size: 52px;
	&_black-theme {
		color: rgba(var(--color-foreground));
	}
}
.article-main {

	letter-spacing: 0.06rem;
	h2,h3,h4 {
	  font-size: 40px;
	  &:not(:last-child) {
		margin-bottom: 1.8rem;
	  }
	}
	p {
	line-height: calc(1 + 0.8 / var(--font-body-scale));
	  &:not(:last-child) {
		margin-bottom: 1.2rem;
	  }
	}

	@media (max-width: $md2 + px) {
		letter-spacing: 0.04rem;
		h2,h3,h4 {
			font-size: 30px;
			&:not(:last-child) {
			  margin-bottom: 1.2rem;
			}
		  }
		  p {
		  line-height: calc(1 + 0.8 / var(--font-body-scale));
			&:not(:last-child) {
			  margin-bottom: 0.8rem;
			}
		  }
	}
  }
