// базовые подключения
@import "vars";
@import "mixins";
@import "fonts";
@import "vendor";
@import "settings";
@import "ui";


// подключения компонентов страницы
@import "./components/header";

.tabs {
  @include tabs;
}


.main {
  flex: 1 1 auto;
  // .main_section

  &_section {
    padding: 25px 0;
  }

  // .main_wrapper
  &_wrapper {
    position: relative;
    z-index: 1;
    background-color: rgba(var(--color-background));
  }
}

.swiper {
  width: 600px;
  height: 600px;
}
.swiper-pagination{
 bottom: 0;
 left: 0;
}
.swiper-pagination img {
  width: 50px;
  height: 50px;
  border: 1px solid #ddd;
  padding: 5px;
  margin: 2px;
}

.heroes {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 100px;
  min-height: 100vh;
  color: rgba(var(--color-foreground));

  // .heroes_body
  &_body {
  }

  // .heroes_bg

  &_bg {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 0;
  }

  // .heroes_content

  &_content {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    text-align: center;
  }

  // .heroes_text

  &_text {
    font-size: 16px;
    h1,h2,h3,h4 {
      &:not(:last-child){
        margin-bottom: 10px;
      }
    }
    p {
      &:not(:last-child){
        margin-bottom: 5px;
      }
    }
    &:not(:last-child){
      margin-bottom: 15px;
    }
  }

  // .heroes_button

  &_button {
  }
}

.in-marvel {

  // .in-marvel_body

  &_body {
  }

  // .in-marvel_cells

  &_cells {
    @media (min-width: $md2 + px) {
      &:nth-child(2n){
        flex-direction: row-reverse;
      }
  }
  @media (max-width: $md2 + px) {
    &:not(:last-child) {
      margin-bottom: 30px;
    }
  } 
  }
}

.cells {
  display: flex;
  align-items: center;
  // .cells_item
  @media (max-width: $md2 + px) {
    flex-direction: column;
  }
  &_item {
    padding: 10px 40px 10px 40px;
    @media (max-width: $md2 + px) {
      padding: 0;
    }
  }

  // .cells_item--img

  &_item--img {
    width: 100%;
    flex: 0 0 50%;
    padding: 0 0 50% 0;
    img {
      border-radius: 6px;
    }
    @media (max-width: $md2 + px) {
      &:not(:last-child) {
        margin-bottom: 15px;
      }
    }
  }

  // .cells_item--img
  
  &_item--slider {

  }
}

.faq {

  // .faq_body

  &_body {
  }

  // .faq_title
  
  &_title {
    &:not(:last-child) {
      margin-bottom: 25px;
    }
  }

  // .faq_items

  &_items {
  }

  // .faq_item

  &_item {
    &:not(:first-child){
      margin-top: 15px;
     }
  }

  // .faq_item-title

  &_item-title {
    font-weight: bold;
  }

  // .faq_item-body

  &_item-body {
    p {
      margin-bottom: 10px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  // .faq_item-title

  &_item-title {
    margin-bottom: 15px;
    &:last-child {
      margin-bottom: 0;
    }
  }
}




